  watch: {
    '$store.state.lang' (value) {
      this.initLanguage()
    },
    'showGameSetItems.selected' (selectItems) {
      // 顯示&隱藏球類設定資訊表
      for (let key in this.mapSportName) {
        // editGameSetItems 是否有 this.mapSportName[i] 這個 key
        if (this.editGameSetItems.hasOwnProperty(key)) {
          // 隱藏表
          if (selectItems.indexOf(key) === -1) this.editGameSetItems[key].show = false
          // 顯示表
          else this.editGameSetItems[key].show = true
        }
      }

      // 「快速設定」disable「顯示修改類別」未勾選的
      let newQuickSettingGameItems = []
      for (let i in this.quickSettingGame.items) {
        if (selectItems.indexOf(this.quickSettingGame.items[i].id) === -1) {
          this.quickSettingGame.items[i].disabled = true
        } else {
          this.quickSettingGame.items[i].disabled = false
          newQuickSettingGameItems.push(this.quickSettingGame.items[i].id)
        }
      }
      let newQuickSettingGameSelected = newQuickSettingGameItems.filter((e)=>{
        return this.quickSettingGame.selected.indexOf(e) > -1
      })
      this.quickSettingGame.selected = newQuickSettingGameSelected
    },
    'Items.whetherAllowLogin.selected' (afterStatus, beforeStatus) {
      // 是否允許登入點到否時須跳提示訊息
      // 總監、大股東、股東、總代理與代理管理帳號頁：【是否確認「鎖定帳號」？ 請注意，下線也會同步執行】
      // 會員管理帳號頁：【是否確認「鎖定帳號」？】
      this.allowLoginIsNoItems.dialogOpen = afterStatus !== beforeStatus && afterStatus === 1 && beforeStatus !== ''
    },
    'Items.whetherAllowBet.selected' (afterStatus, beforeStatus) {
      // 下線允許投注點到否時須跳提示訊息
      // 總監、大股東、股東、總代理與代理管理帳號頁：【是否確認「停押帳號」？ 請注意，下線也會同步執行】
      // 會員管理帳號頁：【是否確認「停押帳號」？】
      this.allowBetIsNoItems.dialogOpen = afterStatus !== beforeStatus && afterStatus === 1 && beforeStatus !== ''
    }
    // getDashboardInfoState () {
    //   this.updateDashboardInfo()
    // }
  },
  computed: {
    ...mapGetters({
      getDashboardInfoState: 'account-management/getDashboardInfoState'
    }),
    // 開放比賽類別-全選
    openSportGameSelectAll: {
      get: function () {
        return this.openSportGameItems.items ? this.openSportGameItems.selected.length === this.openSportGameItems.items.length : false
      },
      set: function (value) {
        var selected = []
        if (value) {
          for (var i in this.openSportGameItems.items) {
            selected.push(this.openSportGameItems.items[i].id)
          }
        }
        this.openSportGameItems.selected = selected
      }
    },
    // 球類設定資訊-顯示修改類別-全選
    showGameSetSelectAll: {
      get: function () {
        return this.showGameSetItems.items ? this.showGameSetItems.selected.length === this.showGameSetItems.items.length : false
      },
      set: function (value) {
        var selected = []
        if (value) {
          for (var i in this.showGameSetItems.items) {
            selected.push(this.showGameSetItems.items[i].id)
          }
        }
        this.showGameSetItems.selected = selected
      }
    },
    // 球類設定資訊-快速設定-全選
    quickSettingGameSelectAll: {
      get: function () {
        let newItems = []
        for (let i in this.quickSettingGame.items) {
          // 只有 enable 項目才可判斷全選
          if (!this.quickSettingGame.items[i].disabled) newItems.push(this.quickSettingGame.items[i])
        }
        return this.quickSettingGame.items ? (newItems.length !== 0) && (this.quickSettingGame.selected.length === newItems.length) : false
      },
      set: function (value) {
        var selected = []
        if (value) {
          for (var i in this.quickSettingGame.items) {
            // 只有 enable 項目才可選擇
            if (!this.quickSettingGame.items[i].disabled) selected.push(this.quickSettingGame.items[i].id)
          }
        }
        this.quickSettingGame.selected = selected
      }
    },
    // disable 球類設定資訊-快速設定-全選
    disableQuickSettingGameSelectAll () {
      let checkItems = []
      for (let i in this.quickSettingGame.items) {
        if (!this.quickSettingGame.items[i].disabled) checkItems.push(this.quickSettingGame.items[i].id)
      }
      return checkItems.length === 0
    },
    // 球類設定資訊-必須選擇一種類別-全選
    quickSettingPlaySelectAll: {
      get: function () {
        return this.quickSettingPlay.items ? this.quickSettingPlay.selected.length === this.quickSettingPlay.items.length : false
      },
      set: function (value) {
        var selected = []
        if (value) {
          for (var i in this.quickSettingPlay.items) {
            selected.push(this.quickSettingPlay.items[i].id)
          }
        }
        this.quickSettingPlay.selected = selected
      }
    }
  },
  methods: {
    ...mapActions({
      getGameSet: 'api/getGameSet',
      getOccupyAndGameSetLimit: 'api/getOccupyAndGameSetLimit',
      putSuperAccount: 'api/putSuperAccount',
      getSuperAccountQuota: 'api/getSuperAccountQuota',
      getMemberTodayAndTomorrowQuota: 'api/getMemberTodayAndTomorrowQuota',
      getBingo: 'api/getBingo',
      getLottery: 'api/getLottery',
      getRoyal: 'api/getRoyal',
      getUpAccountBingo: 'api/getUpAccountBingo',
      getUpAccountLottery: 'api/getUpAccountLottery',
      getUpAccountRoyal: 'api/getUpAccountRoyal',
      getDownAccount: 'api/getDownAccount',
      getUpAccount: 'api/getUpAccount'
    }),
    // 設定錯誤訊息
    showError (msg) {
      this.error.show = true
      this.error.message = msg
    },
    // async init () @看看能不能寫繼承@
    async setAccount (data) {
      return new Promise((resolve, reject) => {
        data.forEach(el => {
          // 帳號資料
          this.userData = el ;
          // 此帳號最後修改時間(修改時間)
          this.Items.lastDateTime = el.time_upd
          // 登入時間(只有會員管理有)
          if (this.level === 1) this.Items.loginTime = el.time_login
          // 投注時間(只有會員管理有)
          if (this.level === 1) this.Items.betTime = el.time_down
          // 帳號
          this.Items.account = el.l_no
          // 名稱
          this.Items.nameItems.name = el.l_name
          // (信用)體育額度
          this.Items.sportCreditItems.amount = el.quota.LCtotal
          // (現金)體育額度
          this.Items.sportCashItems.amount = el.quota.LCtotal
          // 單注下限
          this.Items.downLimitItems.amount = el.down_limit
          // 是否允許登入
          this.Items.whetherAllowLogin.selected = el.l_status
          // 下線允許投注
          this.Items.whetherAllowBet.selected = el.l_close
          // 備註
          this.Items.remark.contents = el.remark
          // 拆帳百分比**在模組元件設置
        });
        resolve()
      });
    },
    // toBack () @看看能不能寫繼承@
    // 取得拆帳百分比修改參數
    checkOccupyEditParams (obj) {
      // for (let key in obj) {
      //   if (!isNaN(parseFloat(obj[key]))) this.editOccupyItems[key] = parseFloat(obj[key])
      // }
      // @202005 以上改成以下，若可行就刪除上面@

      // editOccupyItems 加入修改參數 (但無修改參數會以 false 呈現)
      for (let key in obj) {
        this.editOccupyItems[key] = obj[key]
      }
      // 若值為 false 表示無修改，則移除無修改參數
      let newEditItems = {}
      for (let key in this.editOccupyItems) {
        if (typeof this.editOccupyItems[key] !== 'boolean' && this.editOccupyItems[key] !== '') newEditItems[key] = parseFloat(this.editOccupyItems[key])
      }
      this.editOccupyItems = newEditItems
    },
    // 球類設定資訊表-檢視輸入值驗證狀態
    checkValidStatus (obj) {
      // 各輸入值驗證狀態資料設置
      for (let key in obj) {
        this.isValidItems[key] = obj[key]
      }
      // 計算輸入錯誤值數量來設定整體驗證狀態
