    // 球類設定資訊表-設置修改參數
    setGameSetEditParams (obj, gameType) {
      // editItems 加入修改參數 (但無修改參數會以 false 呈現)
      for (let key in obj) {
        this.editGameSetItems[gameType].editItems[key] = obj[key]
      }
      // 若值為 false 表示無修改，則移除無修改參數
      let newEditItems = {}
      for (let key in this.editGameSetItems[gameType].editItems) {
        if (typeof this.editGameSetItems[gameType].editItems[key] !== 'boolean') newEditItems[key] = parseFloat(this.editGameSetItems[gameType].editItems[key])
      }
      this.editGameSetItems[gameType].editItems = newEditItems
    },
    // 快速修改球類資訊設定表
    changeGameSet (setType) {
      switch(setType){
        case 'pass':
          this.changeGameSetItems.passNumber = this.passItems.selected !== '' ? this.passItems.selected : ''
          break ;
        default:
          if(!this.quickSettingAmount[setType].valid) break ;
          this.changeGameSetItems.amount[setType] = this.quickSettingAmount[setType].amount !== '' ? parseFloat(this.quickSettingAmount[setType].amount) : ''
          break;
      }
      this.changeGameSetItems.gameOption = this.quickSettingGame.selected
      this.changeGameSetItems.playOption = this.quickSettingPlay.selected
      this.changeGameSetItems.setType = setType
    },
    // 金錢數字格式
    amountFormat (value) {
      return numeral(value).format('0.00')
    },
    // 取得額度預設值
    loadQuota () {
      try {
        this.getSuperAccountQuota({ l_no: this.Items.account })
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`取得額度預設值失敗`))
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`取得 ${this.Items.account} 額度預設值成功`, res.data.result)
                if (res.data.result.quota.hasOwnProperty('sport')) {
                  // 設置(體育)額度值(只有會員管理有)
                  if (this.level === 1) this.Items.sportQuota = res.data.result.quota.sport.credit
                  // 設置(體育)剩餘額度值
                  this.Items.sportSurplusQuota = res.data.result.quota.sport.surplus

                  // 更新(信用)體育額度
                  this.Items.sportCreditItems.amount = res.data.result.quota.sport.credit
                  // 更新(現金)體育額度
                  this.Items.sportCashItems.amount = res.data.result.quota.sport.credit
                }
                if (res.data.result.upQuota.hasOwnProperty('sport')) {
                  // 設置(體育)上限剩餘額度值
                  this.Items.sportUpLimitQuota = res.data.result.upQuota.sport.surplus
                }

                if (res.data.result.quota.hasOwnProperty('bingo')) {
                  // 設置(BingoStar)額度值
                  this.Items.bingoQuota = res.data.result.quota.bingo.credit
                  // 設置(BingoStar)剩餘額度值
                  this.Items.bingoSurplusQuota = res.data.result.quota.bingo.surplus
                  // 設置(BingoStar)收回贏額值(只有會員管理有)
                  if (this.level === 1) this.Items.bingoRecoverWin = res.data.result.quota.bingo.recoverWin
                }
                if (res.data.result.upQuota.hasOwnProperty('bingo')) {
                  // 設置(BingoStar)上限剩餘額度值
                  this.Items.bingoUpLimitQuota = res.data.result.upQuota.bingo.surplus
                }

                if (res.data.result.quota.hasOwnProperty('lottery')) {
                  // 設置(Super彩球)額度值
                  this.Items.lotteryQuota = res.data.result.quota.lottery.credit
                  // 設置(Super彩球)剩餘額度值
                  this.Items.lotterySurplusQuota = res.data.result.quota.lottery.surplus
                }
                if (res.data.result.upQuota.hasOwnProperty('lottery')) {
                  // 設置(Super彩球)上限剩餘額度值
                  this.Items.lotteryUpLimitQuota = res.data.result.upQuota.lottery.surplus
                }

                if (res.data.result.quota.hasOwnProperty('royal')) {
                  // 設置(皇家)額度值
                  this.Items.royalQuota = res.data.result.quota.royal.credit
                  // 設置(皇家)剩餘額度值
                  this.Items.royalSurplusQuota = res.data.result.quota.royal.surplus
                  // 設置(皇家)收回贏額值(只有會員管理有)
                  if (this.level === 1) this.Items.royalRecoverWin = res.data.result.quota.royal.recoverWin
                }
                if (res.data.result.upQuota.hasOwnProperty('royal')) {
                  // 設置(皇家)上限剩餘額度值
                  this.Items.royalUpLimitQuota = res.data.result.upQuota.royal.surplus
                }
              }
            }
          })
          .catch(err => {
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
