    },
    // 取得上層帳號資訊
    loadUpAccount () {
      try {
        let account = ''
        account = this.getUpAccountValue()
        // 取不到帳號 return
        if (!account) return
        this.getUpAccount({ balltype: 1, l_no: account })
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`取得上層帳號(%s)失敗`, account))
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`${this.mapLevel[this.level]}管理-上層帳號(${account})資訊:`, res.data.result)
                // 設置上層允許登入狀態
                if (res.data.result.hasOwnProperty('l_status')) this.upStatus.sport = res.data.result.l_status
                // 設置上層允許投注狀態
                if (res.data.result.hasOwnProperty('l_close')) this.upClose.sport = res.data.result.l_close
                // 設置單注下限
                this.Items.downLimitItems.minLimit = res.data.result.down_limit
                // 取到單注下限上層值則 enable
                this.Items.downLimitItems.disabled = false
              }
            }
          })
          .catch(err => {
            console.error(err)
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    // // 取得當前上層帳號值 @看看能不能寫繼承@
    // getUpAccountValue ()
    editAccount () {
      // 修改中狀態(開始)
      this.modifyingStatus = true

      let getSportCodeItems = []
      // 先取得所有修改球類體育代號
      for (let code in this.editGameSetItems) {
        if (!common.isEmpty(this.editGameSetItems[code].editItems)) {
          getSportCodeItems.push(code.split('s')[1])
        }
      }
      // 有修改球類體育代號由小到大排列
      getSportCodeItems.sort(function (a, b) {
        return a - b
      })
      // 只送修改有編輯的球類設定資訊表
      // 說明: function(球類種類, 是否顯示 toastr)
      for (let code in this.editGameSetItems) {
        // editItems 不為空物件則有修改值
        if (!common.isEmpty(this.editGameSetItems[code].editItems)) {
          // 取得有修改的球類體育代號中，最後一個需顯示 toastr
          this.editSuperAccount(code.split('s')[1], parseInt(code.split('s')[1]) === parseInt(getSportCodeItems[getSportCodeItems.length - 1]))
        }
      }
      // 若無修改球類設定資訊表，就直接帶球類體育代號 1，送修改資料
      if (getSportCodeItems.length === 0) {
        this.editSuperAccount(1, true)
      }
    },
    /**
     * 編輯管端帳號
     * @params type        球類種類
     * @params showToastr  是否顯示 toastr
     **/
    editSuperAccount (type, showToastr) {
      let editData = {
        l_no: this.Items.account,
        l_name: this.Items.nameItems.name,
        remark: this.Items.remark.contents,
        balltype: type
      }
      // 單注下限
      if (this.Items.downLimitItems.show) {
        editData.down_limit = this.Items.downLimitItems.amount
      }
      // 允許登入
      if (this.upStatus.sport === 0) editData.l_status = this.Items.whetherAllowLogin.selected
      // 允許投注
      if (this.upClose.sport === 0) editData.l_close = this.Items.whetherAllowBet.selected
      // 拆帳百分比
      if (!common.isEmpty(this.editOccupyItems)) {
        editData = $.extend(editData, this.editOccupyItems)
      }
      // 球類設定資訊
      if (!common.isEmpty(this.editGameSetItems['s' + type].editItems)) {
        editData = $.extend(editData, this.editGameSetItems['s' + type].editItems)
      }

      // console.info(`編輯管端帳號(${type}): `, editData)
      try {
        this.putSuperAccount(editData)
          .then(res => {
            setTimeout(() => {
              if (Object.prototype.toString.call(res) === '[object Error]' &&
                  res.hasOwnProperty('response') &&
                  res.response.status !== 200) {
                // 錯誤訊息
                if (res.response.data.hasOwnProperty('errors')) {
                  common.errorMsgHandler(res.response.data.errors)
                } else {
                  // this.$toastr.error(`${this.$ln(this.mapSportName['s' + type])} ${this.$ln('修改失敗')}`)
                  this.showError(`${this.$ln(this.mapSportName['s' + type])} ${this.$ln('修改失敗')}`)
                }
              } else if (res.status === 200) { // success
                // if (showToastr) this.$toastr.success(this.$ln(`修改成功`), '', {timeOut: 2000, closeButton: false})
                if (showToastr) {
                  this.reload = new Date().getTime()
                  // 清除快速設定
                  this.resetQuickSetting()
                  this.showError(this.$ln(`修改成功`))
                }
                // 修改成功回上一頁
                // this.toBack()
                this.init()
              }

              // 修改中狀態(結束)
              this.modifyingStatus = false
            }, 1000)
          })
          .catch(err => {
            setTimeout(() => {
              if (showToastr) {
                // this.$toastr.error(`${this.$ln(this.mapSportName['s' + type])} ${this.$ln('修改失敗')}`)
                this.showError(`${this.$ln(this.mapSportName['s' + type])} ${this.$ln('修改失敗')}`)
              }

              // 修改中狀態(結束)
              this.modifyingStatus = false
            }, 1000)
          })
      } catch (error) {
        setTimeout(() => {
          // if (showToastr) this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
          if (showToastr) {
            this.showError(this.$ln(`站台資料錯誤，請聯絡客服`))
          }

          // 修改中狀態(結束)
          this.modifyingStatus = false
        }, 1000)
      }
    },
    // 刪除帳號狀態
    deleteAccountStatus (status) {
      if (status) {
        // 刪除成功回上一頁
        this.toBack()
      }
    },
    // 修改額度狀態
    editQuotaStatus (status) {
      if (status) {
        // 載入額度預設值
        this.loadQuota()
        // 載入當日與明日體育額度預設值(只有會員管理有)
        if (this.level === 1) this.loadTodayAndTomorrowQuota()
        this.loadBingoInfo()
        this.loadLotteryInfo()
        this.loadRoyalInfo()
      }
    },
    // 修改賓果狀態
    editBingoStatus (status) {
      if (status) {
        // 載入額度預設值
        this.loadQuota()
        // 載入當日與明日體育額度預設值(只有會員管理有)
        if (this.level === 1) this.loadTodayAndTomorrowQuota()
        this.loadBingoInfo()
      }
    },
    // 修改彩球狀態
    editLotteryStatus (status) {
      if (status) {
        // 載入額度預設值
        this.loadQuota()
        // 載入當日與明日體育額度預設值(只有會員管理有)
        if (this.level === 1) this.loadTodayAndTomorrowQuota()
        this.loadLotteryInfo()
      }
    },
    // 修改皇家狀態
    editRoyalStatus (status) {
      if (status) {
        // 載入額度預設值
        this.loadQuota()
        // 載入當日與明日體育額度預設值(只有會員管理有)
        if (this.level === 1) this.loadTodayAndTomorrowQuota()
        this.loadRoyalInfo()
      }
    },
    // 開啟或關閉修改額度彈窗
    quotaDialogOpen (status) {
      // 載入額度預設值
      this.loadQuota()
      this.loadBingoInfo()
      // this.loadLotteryInfo() // 目前無彩球修改額度功能
      // this.loadRoyalInfo() // 目前無皇家修改額度功能
    },
    // 開啟或關閉賓果彈窗
    bingoDialogOpen (status) {
      // 載入額度預設值
      this.loadQuota()
      this.loadBingoInfo()
      if (status) {
        // (BingoStar)修改前值取得載入狀態(開始)
        this.setApi.bingo.loadingStatus = true
      }
    },
    // 開啟或關閉彩球彈窗
    lotteryDialogOpen (status) {
      // 載入額度預設值
      this.loadQuota()
      this.loadLotteryInfo()
      if (status) {
        // (Super彩球)修改前值取得載入狀態(開始)
        this.setApi.lottery.loadingStatus = true
      }
    },
    // 開啟或關閉皇家彈窗
    royalDialogOpen (status) {
      // 載入額度預設值
      this.loadQuota()
      this.loadRoyalInfo()
      if (status) {
        // (皇家)修改前值取得載入狀態(開始)
        this.setApi.royal.loadingStatus = true
      }
    },
    loadBingoInfo () {
      // 載入[BingoStar]設定修改前值
      this.loadBingoStar()
      // 載入[BingoStar]上層帳號資訊
      this.loadUpAccountBingoStar()
      // 載入球類設定與佔成設定上下限制值
      this.loadDefaultGameSet(16) // BingoStar(修改賓果需該值)
    },
    loadLotteryInfo () {
      // 載入[Super彩球]設定修改前值
      this.loadSuperLottery()
      // 載入[Super彩球]上層帳號資訊
      this.loadUpAccountSuperLottery()
    },
    loadRoyalInfo () {
      // 載入[皇家]設定修改前值
      this.loadRoyal()
      // 載入[皇家]上層帳號資訊
      this.loadUpAccountRoyal()
      // 載入球類設定與佔成設定上下限制值
      this.loadDefaultGameSet(18) // 皇家(修改皇家需該值)
    },
    // 不允許登入
    isNoAllowLogin (status) {
      this.Items.whetherAllowLogin.selected = status ? 1 : 0
      this.allowLoginIsNoItems.dialogOpen = false
    },
    // 不允許投注
    isNoAllowBet (status) {
      this.Items.whetherAllowBet.selected = status ? 1 : 0
      this.allowBetIsNoItems.dialogOpen = false
    },
    // 清除快速設定
    resetQuickSetting () {
      /* 球類設定資訊-顯示修改類別-初始 */
      this.showGameSetItems.selected = []
      for (let i in this.showGameSetItems.items) {
        this.showGameSetItems.selected.push(this.showGameSetItems.items[i].id)
      }

      /* 球類設定資訊-快速設定-初始 */
      this.quickSettingGame.selected = []
      for (let i in this.quickSettingGame.items) {
        this.quickSettingGame.items[i].disabled = false
      }

      /* 球類設定資訊-必須選擇一種類別-初始 */
      this.quickSettingPlay.selected = []

      /* 球類設定資訊-快速設定金額-初始 */
      // 清除金額輸入欄
      for (let i in this.quickSettingAmount) {
        if (this.quickSettingAmount[i].hasOwnProperty('amount')) {
          this.quickSettingAmount[i].amount = ''
        }
      }
      // 初始過關選單
      this.passItems.selected = ''
    },
    // 更新大後台設置資訊
    updateDashboardInfo () {
      let dashboardInfo = JSON.parse(sessionStorage.getItem('super.manage.dashboard.info'))
      if (dashboardInfo &&
          dashboardInfo.hasOwnProperty('wsType') &&
          dashboardInfo.hasOwnProperty('noOpenGame')) {
        /* 註: 先更新站台種類設置再更新開放遊戲設定 */
        // 依照站台種類更新輸入欄(1:現金、2:信用)
        switch (dashboardInfo.wsType) {
          case 1:
            this.Items.sportCashItems.show = true
            this.Items.sportCreditItems.show = false
            break
          case 2:
            this.Items.sportCreditItems.show = true
            this.Items.sportCashItems.show = false
            break
        }
        // 不開放遊戲設定
        for (let i in dashboardInfo.noOpenGame) {
          this.closeGameType(dashboardInfo.noOpenGame[i])
        }

        // 有開放世足
        if (dashboardInfo.noOpenGame.indexOf('s15') === -1) this.openGameType('s15')
        // 有開放 BingoStar
        if (dashboardInfo.noOpenGame.indexOf('s16') === -1) this.openGameType('s16')
        // 有開放 Super彩球
        if (dashboardInfo.noOpenGame.indexOf('s17') === -1) this.openGameType('s17')
        // 有開放皇家
        if (dashboardInfo.noOpenGame.indexOf('s18') === -1) this.openGameType('s18')

        // 單注下限開放設定(只有會員管理不需要設定，因為必顯示)
        if (this.level !== 1) this.Items.downLimitItems.show = dashboardInfo.showItems.downLimit

        // 是否顯示修改密碼功能
        this.passwordItems.show = dashboardInfo.showItems.editPassword
      }
    },
    // 開放遊戲
    openGameType (type) {
      switch (type) {
        // 開放世足
        case 's15':
          break
        // 開放BingoStar
        case 's16':
          // 載入球類設定與佔成設定上下限制值
          this.loadDefaultGameSet(16) // BingoStar(修改賓果需該值)
          break
        // 開放Super彩球
        case 's17':
          break
        // 開放皇家視訊
        case 's18':
          // 載入球類設定與佔成設定上下限制值
          this.loadDefaultGameSet(18) // 皇家(修改皇家需該值)
          break
      }
    },
    // 不開放遊戲
    closeGameType (type) {
      switch (type) {
        // 關閉世足
        case 's15':
          // 不顯示[世足]修改類別 items
          let newShowGameSetItems = []
          for (let i in this.showGameSetItems.items) {
            if (this.showGameSetItems.items[i].id !== 's15') newShowGameSetItems.push(this.showGameSetItems.items[i])
          }
          this.showGameSetItems.items = newShowGameSetItems
          // 不顯示[世足]修改類別 selected
          let selectedItems = []
          for (let i in this.showGameSetItems.selected) {
            if (this.showGameSetItems.selected[i] !== 's15') selectedItems.push(this.showGameSetItems.selected[i])
          }
          this.showGameSetItems.selected = selectedItems
          break
        // 關閉BingoStar
        case 's16':
          this.dashboardOpenStatus.bingo = false
          break
        // 關閉Super彩球
        case 's17':
          this.dashboardOpenStatus.lottery = false
          break
        // 關閉皇家視訊
        case 's18':
          this.dashboardOpenStatus.royal = false
          break
      }
    },
    // 初始語系
    initLanguage () {
      // 說明
      // 越南站(22站)不顯示「元」單位
      this.desc = (!common.isVietnamStation()) ? `說明：如果「返水」為 100，表示每下注一萬元可以得到返水 100 元` : `說明：如果「返水」為 100，表示每下注一萬可以得到返水 100`

      // 名稱資料
      this.Items.nameItems.nameRules = [
        v => !!v || this.$ln(`不能為空`),
        v => ((/^[\u4e00-\u9fa5_A-Za-z0-9\(\)\_\-]+$/.test(v)) || v === '') || this.$ln(`不能為特殊符號或空格，除了「括號()」、「底線_」和「-」`),
        v => v.length <= 20 || this.$ln(`不能超過 20 個字元`)
      ]

      // (體育)信用額度 [信用 / 剩餘額度]
      this.Items.sportCreditItems.suffixLeft = `(${this.$ln('萬')}) / `
      this.Items.sportCreditItems.suffixRight = ` (${this.$ln('萬')})`

      // (體育)現金額度 [現金額度]
      this.Items.sportCashItems.suffixLeft = `${this.$ln(sessionStorage.getItem('super.manage.dashboard.info.unit2'))} / `
      this.Items.sportCashItems.suffixRight = ` ${this.$ln(sessionStorage.getItem('super.manage.dashboard.info.unit2'))}`

      // 單注下限
      this.Items.downLimitItems.rules = [
        v => (v !== '') || this.$ln(`不能為空`),
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`),
        v => (v >= parseFloat(this.Items.downLimitItems.minLimit)) || this.$ln(`要大於等於 %s`, this.Items.downLimitItems.minLimit)
      ]

      // 球類設定資訊-快速設定金額
      // 過濾規則
      this.quickSettingAmount.rules = [
        v => ((/^\d+(\.\d+)*$/.test(v)) || v === '') || this.$ln(`只能數字不能負數和空格`)
      ]
    }
  },
  //BEGIN--生命週期
  beforeCreate: function() {
    //實體初始化
  },
  created: function() {
    //實體建立完成。資料 $data 已可取得，但 $el 屬性還未被建立。
    this.src = this.$options.__file ;
  },
  beforeMount: function() {
    //執行元素掛載之前。
  },
  mounted: function() {
    this.init()

    // 編輯頁跳回列表-記憶頁數
    if (this.$route.query.page) sessionStorage.setItem('account.list.page', this.$route.query.page)
    // 編輯頁跳回列表-記憶每頁筆數
    if (this.$route.query.rowsPerPage) sessionStorage.setItem('account.list.rowsPerPage', this.$route.query.rowsPerPage)
    // 編輯頁跳回列表-記憶選擇狀態
    if (this.$route.query.selectStatus !== undefined) sessionStorage.setItem('account.list.selectStatus', this.$route.query.selectStatus)
  },
  beforeUpdate: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  updated: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  beforeDestroy: function() {
    //實體還可使用。
  },
  destroyed: function() {
    //實體銷毀。
    this.$toastr.options.timeOut = 2000
    this.$toastr.options.closeButton = false
  }
  //END--生命週期
};
</script>

<style lang="scss" scoped>
</style>
