import Api from './api'

export default {
    error: {
        code: '',
        message: '',
    },
    /** @showdoc
     * @catalog api/
     * @title 帳號
     * @name getAccount
     * @description 管端帳號列表 
     * - GET /api/account/super
     * - 管端帳號列表
     * @path src/utils/api/account.js
     * @type methods
     * @param params Object {l_lv:會員等級}
     * @example
     * > let params = { l_lv:this.level }
     * > let res = await _account.getAccount(params);
     */
    async getAccount(params) {
        // 搜尋帳號
        let resp = null;
        const query = Api.queryBuild(params);
        let url = `/account/super?${query}`
        await axios.get(url).then(res => {
            resp = res.response || res;
        }).catch(error => {
            console.log(error.response)
        });

        return Api.getResponse(resp, this.error.message)
    },
}